import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { RestProvider } from '../../providers/rest/rest';
/**
 * Generated class for the ReprogramacionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-reprogramacion',
  templateUrl: 'reprogramacion.html',
})
export class ReprogramacionPage {


motivoReprograma:any='';
  constructor(public navCtrl: NavController, public navParams: NavParams,public loadingCtrl:LoadingController,public apiProvider: RestProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ReprogramacionPage');
  }

  reprogramar(){



  var datE ={estado:2,
              idCita : this.navParams.get('idCita'),
              comentario:this.motivoReprograma};
              console.log(datE);

           let loading = this.loadingCtrl.create({content : "Reprogramando...",   cssClass: "my-loading"});
   loading.present();

        this.apiProvider.cambiarServicioCitaNCREPRO(datE)
      .then(data => {
       console.log(data);
              if(data){
                this.navCtrl.pop();
       }
       else{console.log('Ha ocurrido un error');}
       loading.dismiss();
       
      });




}


}



